@font-face {
  font-family: "Lato", sans-serif;
  src: url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.woff") format("woff");
  font-size: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato", sans-serif;
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
  font-size: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans", sans-serif;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-size: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  position: relative;
  scroll-behavior: smooth; /* плавная прокрутка внутри документа по якорям */
}

* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p, figure, fieldset {
  margin: 0;
}

.container {
  max-width: 1830px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (max-width: 560px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  padding-top: 27px;
  min-height: 61px;
}
@media (max-width: 1600px) {
  .header {
    padding-top: 34px;
    min-height: 149px;
  }
}
@media (max-width: 1024px) {
  .header {
    padding-top: 25px;
    min-height: 115px;
  }
}
@media (max-width: 560px) {
  .header {
    padding-top: 22px;
  }
}
.header__wrapper {
  display: grid;
  grid-template-columns: auto auto 757px auto;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 30px;
}
@media (max-width: 1600px) {
  .header__wrapper {
    grid-template-columns: auto 700px;
    grid-template-areas: "logo icons" "btn search";
    row-gap: 35px;
    padding-bottom: 20px;
  }
}
@media (max-width: 1024px) {
  .header__wrapper {
    row-gap: 29px;
  }
}
@media (max-width: 920px) {
  .header__wrapper {
    grid-template-columns: auto minmax(540px, 700px);
    column-gap: 30px;
  }
}
@media (max-width: 768px) {
  .header__wrapper {
    grid-template-columns: auto minmax(280px, 540px);
    column-gap: 20px;
  }
}
@media (max-width: 560px) {
  .header__wrapper {
    padding-bottom: 20px;
    row-gap: 20px;
  }
}
@media (max-width: 440px) {
  .header__wrapper {
    grid-template-columns: auto auto;
    grid-template-areas: "logo ." "btn icons" "search search";
  }
}
.header__logo {
  padding-top: 5px;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 41px;
  letter-spacing: 0.06em;
  color: #2D2D2D;
  margin-right: 12px;
}
@media (max-width: 1600px) {
  .header__logo {
    margin-right: 0;
    padding-top: 0;
    grid-area: logo;
  }
}
@media (max-width: 1024px) {
  .header__logo {
    font-size: 26px;
    line-height: 35px;
    grid-column: 1/3;
    grid-row: 1/2;
  }
}
@media (max-width: 560px) {
  .header__logo {
    font-size: 18px;
    line-height: 25px;
  }
}
.header__logo_color {
  color: #3670C7;
}
.header__btn {
  display: flex;
  justify-content: space-between;
  width: 151px;
  height: 54px;
  padding: 10px 28px 12px 28px;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.5;
}
@media (max-width: 1600px) {
  .header__btn {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
@media (max-width: 1024px) {
  .header__btn {
    width: 115px;
    height: 42px;
    font-size: 16px;
    padding: 9px 21px 11px 21px;
  }
}
@media (max-width: 560px) {
  .header__btn {
    width: 79px;
    height: 30px;
    font-size: 12px;
    padding: 6px 14px 7px 14px;
    border-radius: 4px;
  }
}
.header__btn-open {
  align-self: center;
}
@media (max-width: 560px) {
  .header__btn-open {
    width: 14px;
    height: 14px;
  }
}
.header__btn-close {
  align-self: center;
  display: none;
}
@media (max-width: 560px) {
  .header__btn-close {
    width: 14px;
    height: 14px;
  }
}

.header__btn_active > .header__btn-open {
  transition: 0.5s ease-in-out;
  display: none;
}

.header__btn_active > .header__btn-close {
  transition: 0.5s ease-in-out;
  display: block;
}

.btn {
  background-color: #3670C7;
  color: #ffffff;
  border-radius: 10px;
  border: none;
}
.btn:hover, .btn:active {
  background-color: #5D92E0;
}
.btn:focus {
  background-color: #3B65A5;
}

.search {
  width: 100%;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1600px) {
  .search {
    grid-area: search;
    justify-self: end;
  }
}
.search__input {
  box-sizing: border-box !important;
  display: block;
  width: 100%;
  height: 54px;
  padding-left: 40px;
  background: #EFEFEF;
  border-radius: 30px;
  border: 1px solid #EFEFEF;
  outline: none;
  color: #2D2D2D;
  font-weight: 700;
}
@media (max-width: 1024px) {
  .search__input {
    height: 42px;
  }
}
@media (max-width: 560px) {
  .search__input {
    height: 30px;
    padding-left: 15px;
    padding-bottom: 6px;
  }
}
.search__input::placeholder {
  font-weight: 400;
  font-size: 16px;
  color: #8F8F8F;
}
@media (max-width: 560px) {
  .search__input::placeholder {
    font-size: 8px;
  }
}
.search__button {
  width: 101px;
  height: 54px;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #3670C7;
  border-radius: 30px;
  border: none;
  padding-top: 6px;
}
@media (max-width: 1600px) {
  .search__button {
    padding-top: 2px;
  }
}
@media (max-width: 1024px) {
  .search__button {
    width: 82px;
    height: 42px;
  }
}
@media (max-width: 560px) {
  .search__button {
    width: 50px;
    height: 30px;
  }
}
@media (max-width: 1024px) {
  .search__img {
    width: 22px;
    height: 22px;
  }
}
@media (max-width: 560px) {
  .search__img {
    width: 15px;
    height: 15px;
  }
}

.icons {
  width: 354px;
  height: 61px;
}
@media (max-width: 1600px) {
  .icons {
    grid-area: icons;
    justify-self: end;
    width: 294px;
  }
}
@media (max-width: 1024px) {
  .icons {
    width: 221px;
    height: 44px;
  }
}
@media (max-width: 560px) {
  .icons {
    width: 130px;
    height: 30px;
  }
}
.icons__list {
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: space-between;
}
.icons__link {
  min-height: 61px;
  display: grid;
  grid-template-rows: repeat(2, auto);
  align-content: space-between;
}
@media (max-width: 1024px) {
  .icons__link {
    min-height: 44px;
  }
}
@media (max-width: 560px) {
  .icons__link {
    min-height: 30px;
  }
}
.icons__img {
  justify-self: center;
  width: 36px;
  height: 36px;
  position: relative;
}
@media (max-width: 1024px) {
  .icons__img {
    width: 30px;
    height: 30px;
  }
}
.icons__img_count::before {
  content: "2";
  display: flex;
  justify-content: center;
  align-items: center;
  width: 14px;
  height: 14px;
  background: #ffffff;
  border-radius: 100%;
  position: absolute;
  top: 0;
  right: 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 130%;
  color: #3670C7;
  text-align: center;
}
@media (max-width: 1600px) {
  .icons__img_count::before {
    top: 2px;
  }
}
@media (max-width: 560px) {
  .icons__img_count::before {
    top: 0;
  }
}
.icons__text {
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  color: #2D2D2D;
  justify-self: center;
}
@media (max-width: 1600px) {
  .icons__text {
    line-height: 1;
  }
}
@media (max-width: 1024px) {
  .icons__text {
    font-size: 12px;
  }
}
@media (max-width: 560px) {
  .icons__text {
    display: none;
  }
}

.icons__link:hover .icons__img svg {
  fill: #3670C7;
}

.icons__link:hover .icons__text {
  color: #3670C7;
}

.navigation {
  color: #2D2D2D;
  padding-top: 22px;
  display: none;
}
.navigation__wrapper {
  display: grid;
  gap: 40px 220px;
  grid-template-columns: 535px max-content max-content;
  grid-template-areas: "title-catalog title-users title-contacts" "list-catalog list-users contact";
}
@media (max-width: 1600px) {
  .navigation__wrapper {
    gap: 40px;
    justify-content: space-between;
  }
}
@media (max-width: 1200px) {
  .navigation__wrapper {
    gap: 30px 80px;
    justify-content: start;
    grid-template-columns: 500px max-content;
    grid-template-areas: "title-catalog title-users" "list-catalog list-users" "title-contacts ." "contact .";
  }
}
@media (max-width: 1024px) {
  .navigation__wrapper {
    grid-template-columns: 400px max-content;
  }
}
@media (max-width: 768px) {
  .navigation__wrapper {
    gap: 30px;
    grid-template-columns: 1fr;
    grid-template-areas: "title-catalog" "list-catalog" "title-users" "list-users" "title-contacts" "contact";
  }
}
.navigation__title {
  font-size: 32px;
  font-weight: 700;
  line-height: 1.3;
}
@media (max-width: 1024px) {
  .navigation__title {
    font-size: 26px;
  }
}
@media (max-width: 560px) {
  .navigation__title {
    font-size: 20px;
  }
}
.navigation__title_catalog {
  grid-area: title-catalog;
}
.navigation__title_users {
  grid-area: title-users;
}
.navigation__title_contacts {
  grid-area: title-contacts;
}
.navigation__list {
  font-size: 22px;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 20px 83px;
  max-height: 297px;
}
@media (max-width: 1600px) {
  .navigation__list {
    gap: 20px 50px;
  }
}
@media (max-width: 1024px) {
  .navigation__list {
    font-size: 18px;
    gap: 20px 30px;
    max-height: 260px;
  }
}
@media (max-width: 768px) {
  .navigation__list {
    flex-direction: row;
    max-height: fit-content;
  }
}
@media (max-width: 560px) {
  .navigation__list {
    font-size: 14px;
  }
}
.navigation__list_catalog {
  grid-area: list-catalog;
}
.navigation__list_users {
  grid-area: list-users;
}
.navigation__list_contacts {
  grid-area: contact;
}
.navigation__item {
  max-width: 225px;
}
@media (max-width: 1024px) {
  .navigation__item {
    max-width: 180px;
  }
}
@media (max-width: 768px) {
  .navigation__item {
    max-width: fit-content;
  }
}
.navigation_active {
  display: block;
}

.bread {
  margin-bottom: 80px;
  display: flex;
  align-items: center;
}
@media (max-width: 1200px) {
  .bread {
    margin-bottom: 60px;
  }
}
@media (max-width: 880px) {
  .bread {
    margin-bottom: 50px;
  }
}
@media (max-width: 560px) {
  .bread {
    margin-bottom: 30px;
  }
}
.bread__link {
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  color: #525252;
  margin-right: 10px;
}
.bread__link:hover {
  color: #3670C7;
}
@media (max-width: 880px) {
  .bread__link {
    font-size: 14px;
    margin-right: 15px;
    max-height: 20px;
    max-width: 130px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
@media (max-width: 560px) {
  .bread__link {
    font-size: 8px;
    max-height: 10px;
  }
}
.bread__img {
  margin-right: 10px;
}
@media (max-width: 880px) {
  .bread__img {
    margin-right: 15px;
  }
}
@media (max-width: 560px) {
  .bread__img {
    width: 11px;
    height: 11px;
  }
}

.banner {
  padding: 60px 0;
}
@media (max-width: 1200px) {
  .banner {
    padding: 50px 0;
  }
}
@media (max-width: 768px) {
  .banner {
    padding: 40px 0;
  }
}
@media (max-width: 440px) {
  .banner {
    padding-top: 18px;
    padding-bottom: 20px;
  }
}
.banner__wrapper {
  background-image: url("../img/banner.jpg");
  background-image: image-set(url("../img/banner.avif") 1x, url("../img/banner.webp") 1x, url("../img/banner.jpg") 1x);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 20px;
  min-height: 480px;
  padding-top: 152px;
}
@media (max-width: 1200px) {
